      },
    },
  },
  //  emitter
  inject: ["emitter"],
  methods: {
    // 
    goUserHomePage() {
      // 
      this.$swal
        .fire({
          title: "",
          text: "",
          icon: "info",
          showCancelButton: true,
          confirmButtonColor: "#3085d6",
          cancelButtonColor: "#d33",
          cancelButtonText: "",
          confirmButtonText: "",
        })
        .then((result) => {
          if (result.isConfirmed) {
            this.$swal.fire("!", "", "success");
            this.$router.push("/");
          }
        });
    },
  },
  created() {
    // Navbar
    this.$emit("change-navbar-page-title", "");
    // Navbar
    this.$emit("change-navbar-btn-title", "");
    // Navbar 
    this.emitter.on("open-modal", (status) => {
      if (status === "") {
        this.goUserHomePage();
      }
    });
  },
};
